import interfaceurl from '@/utils/interface.js'
import navigationBar from '@/components/navigation-bar.vue' //引入自定义导航栏
export default {
	components: {
		navigationBar
	},
	data() {
		return {
			//设置导航栏样式
			navigationBarStyle: {
				iconText: '我的积分' //导航栏文字
			},
			params: {
				page: 1, //页数
				size: 10, //每页几条
				type: 2 //1佣金,2积分
			}, //分页参数
			pointsData: {}, //积分数据
			pointsList: [], //积分列表
			userInfo: {} //获取用户授权信息
		}
	},
	onLoad() {
		this.userInfo = uni.getStorageSync('userInfo')
	},
	onShow() {
		this.initData()
	},
	//到达页面底部时触发的事件
	onReachBottom() {
		if (this.pointsList.length >= this.pointsData.total) {
			return;
		}
		this.params.page++;
		this.getPoints()
	},
	methods: {
		initData() {
			//重置分页参数
			this.pointsData = {}
			this.pointsList = []
			this.params.page = 1
			this.getPoints();
		},
		getPoints() {
			let that = this;
			interfaceurl.checkAuth(interfaceurl.profitRecord, this.params).then((res) => {
				that.pointsData = res.data;
				if(that.params.page == 1) {
					that.pointsList = res.data.data
				} else {
					that.pointsList = that.pointsList.concat(res.data.data)
				}
			});
		}
	}
}
